@media only screen and (max-width: 1030px) {
    .flex {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;
        flex-direction: column;
        width: 100vw;
    }

    .logo {
        height: 15vh;
        display: flex;
        align-items: center;
        justify-content: center;
        width: auto;
    }

    .log {
        font-size: 20px;
        text-align: center;
        padding-right: 30px;
        width: 113px;
        color: rgb(105, 105, 105);
    }

    #live {
        width: 100vw;
    }

    .login {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 30px 0px 15px 0px;
    }

    #element {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .breads {
        margin: 0px 0px 15px 5px !important;
    }


    .paper {
        height: 30vh;
        width: 78vw;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .textfield {
        width: 67vw;
        margin: 11px 0px -6px 0px !important;
    }

    .verticalline {
        background-color: rgba(187, 177, 177, 0);
        height: 27px;
        margin: 0px 0px 0px 10px;
    }
}
