.maindiv {
    height: 48vh;
    width: 86vw;
    background-color: #f4f5f5;
    margin: 24px 0px 5px 0px;
    padding: 0px 0vw 0px 14vw;
}

.image {
    border-radius: 50%;
}

.image img {
    height: 9rem;
    border-radius: 50%;
}

.food {
    width: 13vw;
    padding: 0rem;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.food p {
    text-align: center;
    font-size: 25px;
    margin: auto;
    color: black;
}

.sub-div {
    margin: 24px 0px 5px 0px;
    display: flex;
    height: 28vh;
    width: 73vw;
}

.maindiv2 {
    height: 48vh;
    width: 80vw;
    margin: 24px 0px 5px 0px;
    padding: 0px 0vw 0px 14vw;
}

.sub-div1 {
    margin: 24px 0px 5px 0px;
    display: flex;
    height: 28vh;
    width: 73vw;
}

/* .paper{
  border-radius: 50%;
} */
.best-delhi{
    margin-left: 15vw;
}
.card {
    height: 400px;
    width: 350px;
    border-radius: 5%;
    margin: 10px 1px 10px 15px;
    padding: 0px 10px 0px 0px;
}

.img-material img {
    width: 340px;
    border-radius: 5%;
    margin: 10px;
    height: 215px;
}

.txt {
    height: 93px;
    width: 340px;
    margin: 0px 10px 0px 10px;
    display: flex;
}

.card-left {
    height: 99px;
    width: 170px;
    display: flex;
    flex-direction: column;
}

.top-left {
    height: 33px;
    width: 170px;
}
.top-left p{
    color: black;
    margin: 0px 0px 0px 11px;
}
.bottom-left {
    height: 66px;
    width: 170px;
}
.bottom-left p{
    font-size: 13px;
    margin: 0px 0px 0px 13px;
}
.card-right {
    height: 99px;
    width: 170px;
    display: flex;
    flex-direction: column;
}
.top-right{
    height: 33px;
    width: 170px;
}
.style-rating{
    margin: 0px 0px 0px 30px;
}
.center-right{
    height: 33px;
    width: 170px;
}
.center-right p{
    font-size: 16px;
    margin: 2px 26px 0px 0px;
    text-align: end;
}
.bottom-right{
    height: 33px;
    width: 170px;
}
.bottom-right p{
    text-align: end;
    margin: -8px 27px 0px 0px;
    font-size: 14px;
    color: black;
}
.left-bottom{
height: 0.9px;
width: 302px;
background-color: #c2c2c2;
margin: 6px 0px 0px 22px;
}
.txt1 p{
    font-size: 13px;
margin: 10px 30px 0px 20px;
text-align: center;
}
.card-manager{
    height: fit-content;
    width: 86vw;
    display: flex;
    flex-wrap: wrap;
    padding: 0vw 0vw 0vw 14vw;
}
.card:hover{
    box-shadow: 3px 2px 10px 2px rgb(187, 187, 187);
}