.flex {
    display: flex;
    align-items: center;
}

.logo img {
    width: 140px;
}

.logo {
    height: 15vh;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 23vw;
}

.location {
    color: rgb(234, 108, 120);
}

.paper {
    display: flex;
    align-items: center;
    /* justify-content: center; */
    margin: 0px 0px 0px 15px;
    padding-right: 10px;
    width: 45vw;
    height: 7vh;
}

.paper svg {
    margin: 0px 8px 0px 9px;

}

.textfield {
    margin: 7px 0px 0px 0px;
    height: 41px;
    width: 18vw;
    font-size: 2px;
    border-color: transparent;
}
.verticalline{
    background-color: #bbb1b1;
height: 27px;
width: 0.1rem;
margin: 0px 0px 0px 10px;
}
.login{
display: flex;
align-items: center;
}
.log{
    font-size: 20px;
    text-align: end;
    padding-right: 30px;
width: 200px;
color: rgb(105, 105, 105);
}
.sign{
    text-align: start;
    padding-left: 20px;
width: 100px;
font-size: 20px;
color: rgb(105, 105, 105);
}
.breads{
    margin: 0px 0px 20px 14vw !important;
    font-size: 12px !important;
}
.bread:hover{
 color: rgb(234, 108, 120) !important;
}