.delivery-tab{
    width: 17vw;
height: 12vh;
display: flex;
align-items: flex-start;
justify-content: center;
}
.delivery-tab img{
    width: 2vw;
    height: 4vh;
}
.delivery-tab p{
    font-size: 23px;
    margin: 13px 0px 0px 16px;
    color: rgb(239, 79, 95);
}
.img-border{
    background-color: rgb(252, 238, 192);
height: 8vh;
width: 4vw;
display: flex;
align-items: center;
justify-content: center;
border-radius: 50%;
}
.main{
    display: flex;
    align-items: center;
}
.left{
    height: 10vh;
width: 30vw;
display: flex;
align-items: center;
justify-content: flex-end;
}
.center{
    height: 10vh;
width: 18vw;
display: flex;
align-items: center;
justify-content: center;
}
.right{
    height: 10vh;
width: 30vw;
display: flex;
align-items: center;
justify-content: flex-start;
}
.pink-line{
    height: 3px;
    width: 16vw;
    margin: 0px 0px 0px 14vw;
    background-color: rgb(239, 79, 95);
}
.hr{
    color: rgba(232, 226, 226, 0.638);
    margin: 0px 0px 10px 0px;
}
.delivery-tab button{
    font-size: 23px;
    margin: 13px 0px 0px 16px;
    color: rgb(239, 79, 95);
    border: 0px transparent;
    background-color: transparent;
    cursor: pointer;
}