@media only screen and (max-width: 1030px) {
    .main {
        display: flex;
        align-items: center;
        flex-direction: column;
      }
      .left {
        height: 11vh;
        width: 55vw;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .img-border {
        background-color: rgb(252, 238, 192);
        height: fit-content;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        width: fit-content;
      }
      .delivery-tab {
        width: 56vw;
        height: 12vh;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .delivery-tab img {
        width: 10vw;
        height: 5vh;
      }
      .delivery-tab button {
        font-size: 23px;
        /* margin: 13px 0px 0px 16px; */
        color: rgb(239, 79, 95);
        border: 0px transparent;
        background-color: transparent;
        cursor: pointer;
        width: fit-content;
      }
      .center {
        width: 56vw;
        height: 12vh;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .right {
        width: 56vw;
        height: 12vh;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .pink-line {
        height: 3px;
        width: 100vw;
        margin: 0px !important;
        background-color: rgb(239, 79, 95);
      }
}
