@media only screen and (max-width: 1030px) {
    .dine-h2 {
        height: fit-content;
        display: flex;
        flex-direction: column;
    }
    .h2-white {
        width: auto;
        height: fit-content;
        font-size: 11px;
        color: #272727;
    }
    .h2-pink {
        width: fit-content;
        height: fit-content;
        color: rgb(234, 108, 120);
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .h2-pink h3 {
        text-align: start;
    }
    .dine-main {
        width: 100vw;
        height: fit-content;
        display: flex;
        justify-content: center;
    }
    .crousal {
        height: fit-content;
        width: fit-content;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
    }
    .dine-image1 {
        height: fit-content;
        width: fit-content;
        border-radius: 5%;
        background-size: cover;
        background-position: center;
        margin: 3px 7px 20px 7px;
    }
    .dine-image2 {
        height: fit-content;
        width: fit-content;
        border-radius: 5%;
        background-size: cover;
        background-position: center;
        margin: 0px 7px 14px 0px;
    }
    .dine-image3 {
        height: fit-content;
        width: fit-content;
        border-radius: 5%;
        background-size: cover;
        background-position: center;
        margin: 0px 7px 14px 0px;
    }
    .dine-image4 {
        height: fit-content;
        width: fit-content;
        border-radius: 5%;
        background-size: cover;
        background-position: center;
        margin: 0px 7px 14px 0px;
    }
    .dine-sub {
        width: 72vw;
        height: fit-content;
    }
    .gold-box {
        width: 76vw;
        display: flex;
        flex-direction: column;
    }
    .imp{
        background-color: #272727 !important;
    }
.gold-box2 {
    width: 83vw;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
}
.offer-box img {
    height: 20vh;
    width: 90vw;
}

}