@media only screen and (max-width: 1030px) {

    .maindiv {
        height: fit-content;
        width: 86vw;
        background-color: #f4f5f5;
        margin: 24px 0px 5px 0px;
        padding: 0px 0vw 0px 14vw;
    }

    .sub-div {
        margin: 24px 0px 5px 0px;
        display: flex;
        height: fit-content;
        width: 73vw;
        flex-wrap: wrap;
        justify-content: space-around;
      }
      .food {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: fit-content;
      }
      .maindiv2 {
        height: fit-content;
        width: 86vw;
        /* background-color: #f4f5f5; */
        margin: 24px 0px 5px 0px;
        padding: 0px 0vw 0px 14vw;
    }

    .sub-div1 {
        margin: 24px 0px 5px 0px;
        display: flex;
        height: fit-content;
        width: 73vw;
        flex-wrap: wrap;
        justify-content: space-around;
      }
      .card-manager {
        height: fit-content;
        width: 77vw;
        display: flex;
        flex-wrap: wrap;
        /* padding: 0vw 0vw 0vw 7vw; */
        justify-content: center;
      }
}
@media only screen and (max-width: 1080px) {
  .card {
    height: 400px;
    width: 310px;
    border-radius: 5%;
    margin: 10px 1px 10px 15px;
    padding: 0px 10px 0px 0px;
  }
  .img-material img{
      width: 290px;
  }
  .txt {
    height: 93px;
    width: 310px;
    margin: 0px 10px 0px 10px;
    display: flex;
  }
  .card-left {
    height: 99px;
    width: 155px;
    display: flex;
    flex-direction: column;
  }
  .card-right {
    height: 99px;
    width: 155px;
    display: flex;
    flex-direction: column;
  }
  .top-left {
    height: 33px;
    width: 155px;
  }
  .bottom-left {
    height: 66px;
    width: 155px;
  }
  .style-rating {
    margin: 0px 0px 0px 5px;
  }
  .center-right {
    height: 33px;
    width: 155px;
  }
  .bottom-right {
    height: 33px;
    width: 153px;
  }
}
